



















import { Component, Vue } from "vue-property-decorator";

@Component
class AuthModal extends Vue {
  show = false;

  handleShowAuthModal() {
    this.show = true;
  }

  handleAuth(wallet: "fennec" | "mixin" | "links") {
    if (wallet === "fennec") {
      alert("use fennec auth");
    } else {
      alert("use mixin or link auth");
    }
  }
}
export default AuthModal;
